<template>
  <div class="main">
    <el-button type="primary" @click="openDrawer()">Alterar Colunas</el-button>
    <!-- Fields -->
    <el-drawer
      title="Edição de Colunas"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
    >
      <div style="padding:0px 20px;">
        <p>Selecione os campos que deseja mostrar na listagem:</p>
        <p v-for="field in this.fields" :key="field.field_id">
          <el-checkbox v-model="fieldsSelected[field.field_table_name]">{{ field.field_label }}</el-checkbox>
        </p>

        <el-button type="primary" @click="alterColumns()">Alterar Tabela</el-button>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      drawer: false,
      fieldsSelected: {},
      direction: "rtl",
      fields: [],
    };
  },
  props: ["module"],
  methods: {
    alterColumns() {
      let fieldsSelected = Object.entries(this.fieldsSelected);
      let someTrue = false;

      fieldsSelected.forEach((el) => {
        if (el[1] == true) {
          someTrue = true;
          return false;
        }
      });

      if (someTrue == true) {
        this.$emit("listColumns", this.fieldsSelected);
        this.fieldsSelected = {};
        this.fields = [];
        this.drawer = false;
      } else {
        this.$notify.error({
          title: 'Erro',
          message: 'Selecione ao menos 1 campo'
        });
      }
    },
    openDrawer() {
      this.$http.get("fields/" + this.module).then((res) => {
        this.fields = res.data;

        this.fields.forEach((element) => {
          this.$set(this.fieldsSelected, element.field_table_name, false);
        });
      });
      this.drawer = true;
    },
    handleClose(done) {
      this.$confirm("Você tem certeza que quer fechar a edição de campos?")
        .then((_) => {
          _;
          this.drawer = false;
          done();
        })
        .catch((_) => {
          _;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.main {
  margin-left: 10px;
  padding: 20px;
  background: #fff;
}
</style>