<template>
  <div>
    <el-row>
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>
      <el-col id="listData" v-loading="this.loading" :xs="24" :sm="24" :md="18" :lg="18" :xl="18">
        <el-row>
          <el-col :span="24">
            <el-popconfirm
              confirmButtonText="Excluir"
              cancelButtonText="Cancelar"
              title="Você tem certeza que deseja excluir os leads selecionados?"
              @onConfirm="deleteLeads()"
            >
              <el-button slot="reference" type="primary">Excluir</el-button>
            </el-popconfirm>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="tableData"
              style="width: 100%"
              @selection-change="handleSelectionChange"
            >
              <el-table-column type="selection" width="55"></el-table-column>

              <el-table-column
                v-for="col in tableColumns"
                :key="col.field_label"
                :prop="col.field_table_name"
                :label="col.field_label"
              ></el-table-column>

              <el-table-column width="70">
                <template slot-scope="scope">
                  <router-link :to="linkToView(scope.row.lead_id)">
                    <el-button size="small">
                      <i class="el-icon-right"></i>
                    </el-button>
                  </router-link>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <el-row id="paginationList">
          <el-col :span="24" v-if="this.pagination()">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="1000"
              :page-size="this.pageSize"
              :current-page="this.currentPage"
            ></el-pagination>
          </el-col>
        </el-row>
      </el-col>
      <el-col :xs="24" :sm="24" :md="6" :lg="6" :xl="6">
        <FieldsBar module="leads" @listColumns="alterColumns" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import FieldsBar from "@/views/restricted/Components/FieldsBarForList";
export default {
  name: "Leads",
  components: { FieldsBar },
  data() {
    return {
      pageSize: 0,
      pages: 0,
      currentPage: 0,
      countTotalLeads: 0,
      pageTitle: "Leads",
      loading: true,
      tableData: [],
      multipleSelection: [],
      columns: "",
      tableColumns: [
        {
          field_label: "Nome",
          field_table_name: "first_name",
        },
        {
          field_label: "Sobrenome",
          field_table_name: "last_name",
        },
        {
          field_label: "E-mail",
          field_table_name: "email",
        },
        {
          field_label: "Telefone",
          field_table_name: "work_number",
        },
      ],
    };
  },
  head: {
    title: {
      inner: "Leads",
      complement: "GuiaSales",
    },
  },
  methods: {
    alterColumns(payload) {
      this.columns = payload;
      this.getLeads();
    },
    renderTable(cols = false) {
      let url = "fields/leads";

      if (cols != false) {
        url += "?cols=" + cols;
      }

      this.$http.get(url).then((res) => {
        this.tableColumns = res.data;
      });
    },
    getLeads() {
      let url = "/leads/view";

      if (this.columns != "") {
        let cols = Object.entries(this.columns);

        let queryCols = "";
        let count = 0;

        cols.forEach((col) => {
          if (col[1] == true) {
            if (count != 0) {
              queryCols += ",";
            }
            queryCols += col[0];
            count++;
          }
        });

        if (count != 0) {
          queryCols += ",";
        }

        queryCols += "lead_id";

        url += "?fields=" + queryCols;
        this.renderTable(queryCols);
      } else {
        this.renderTable();
      }

      this.$http
        .get(url)
        .then((res) => {
          this.loading = false;
          if (res.status == 200) {
            this.tableData = res.data.data;
            this.pages = res.data.meta.total_pages;
            this.currentPage = res.data.meta.page;
            this.pageSize = res.data.meta.per_page;
            this.countTotalLeads = res.data.meta.total_leads;
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error.response);
        });
    },
    deleteLeads() {
      if (this.multipleSelection.length == 0) {
        this.$message({
          message: "Selecione ao menos 1 registro",
          type: "warning",
        });
        return false;
      }

      this.$http.post("/leads/bulk", this.multipleSelection).then((res) => {
        if (res.status == "200") {
          this.getLeads();
          this.$notify({
            title: "Sucesso",
            message: res.data.msg,
            type: "success",
          });
        }
      });
    },
    linkToView(id) {
      return {
        name: "Lead_View",
        params: { id: id },
      };
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    pagination() {
      if (this.pages > 1) {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    this.getLeads();
  },
};
</script>

